import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';


import {BrowserRouter} from 'react-router-dom';//라이브러리 이름 경로없는것. 
//HashRouter : 주소 뒤에 #이 보안용목적. 
